import type {
  Location,
  StaffMember,
  LocationHours,
  WpPageInfo,
} from "../generated/graphql";

// Re-export generated types for convenience
export type { Location, StaffMember, LocationHours };

// Alias for backward compatibility
export type WPLocation = Location;
export type ClinicalStaff = StaffMember;
export type Chiropractor = StaffMember;

export const ALL_LOCATIONS_QUERY = `
  query AllLocations($first: Int, $after: String) {
    locations(first: $first, after: $after) {
      nodes {
        id
        databaseId
        slug
        title
        content
        shortDescription
        city
        state
        streetAddress
        zip
        phone
        email
        coordinates {
          lat
          lng
        }
        ... on NodeWithFeaturedImage {
          featuredImage {
            node {
              sourceUrl
              altText
              mediaDetails {
                width
                height
              }
            }
          }
        }
      }
      pageInfo {
        hasNextPage
        endCursor
        hasPreviousPage
        startCursor
      }
    }
  }
`;

export const LOCATION_BY_SLUG_QUERY = `
  query LocationBySlug($slug: ID!) {
    location(id: $slug, idType: SLUG) {
      id
      databaseId
      slug
      title
      content
      streetAddress
      city
      state
      zip
      phone
      email
      shortDescription
      servicesOffered
      heroUnit {
        heroLink {
          target
          title
          url
        }
        heroLinkIcon {
          node {
            altText
            sourceUrl
            slug
            mediaDetails {
              width
              height
            }
          }
        }
        heroLink2 {
          target
          title
          url
        }
        heroLinkIcon2 {
          node {
            sourceUrl
            altText
            slug
            mediaDetails {
              width
              height
            }
          }
        }
      }
      coordinates {
        lat
        lng
      }
      hours {
        day
        open
        close
      }
      ... on NodeWithFeaturedImage {
        featuredImage {
          node {
            sourceUrl
            altText
            description
            slug
            title
            mediaDetails {
              width
              height
            }
          }
        }
      }
      clinicalStaff {
        nodes {
          id
          databaseId
          title
          staffType
          jobTitle
          credentials
          specialties
          servicesOffered
          bio
          acceptingPatients
          headshot {
            sourceUrl
            altText
          }
        }
      }
    }
  }
`;

export const ALL_LOCATION_SLUGS_QUERY = `
  query AllLocationSlugs {
    locations(first: 1000) {
      nodes {
        id
        slug
      }
      pageInfo {
        hasNextPage
        endCursor
      }
    }
  }
`;

// Response types for queries
export type AllLocationsResponse = {
  locations?: {
    nodes?: Location[];
    pageInfo?: WpPageInfo;
  };
};

export type LocationBySlugResponse = {
  location?: Location | null;
};

export type AllLocationSlugsResponse = {
  locations?: {
    nodes?: Array<{ id?: string; slug?: string }>;
    pageInfo?: WpPageInfo;
  };
};
